


.navbar {
    padding: 0.5rem 1rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.logo-box {
    margin-left: 50px;
}

.logo {
    font-size: 30px;
    font-weight: bold;
    cursor: pointer;
    transition: 1s;

}
.logo-m, .logo-n, .logo-k {
    transition: 0.5s;
}

.logo:hover .logo-m {
    translate : 2px 0 0;
    scale: 1.2;
    transform: scale 1s linear, translate 1s linear;
    transition: 0.5s;
    text-shadow: 0 0 25px var(--clro);
}

.logo:hover .logo-n {
    translate: 0 10px 0;
    scale: 1.2;
    transform: scale 1s linear, translate 1s linear;
    transition: 0.5s;
    color: var(--clro);
    text-shadow: 0 0 25px var(--bgs);
    font-weight: 900;
    /* font-size: 35px; */
}

.logo:hover .logo-k {
    translate: -2px 0 0;
    scale: 1.2;
    transform: scale 1s linear, translate 1s linear;
    transition: 0.5s;
    text-shadow: 0 0 25px var(--clro);
}

.logo > a > p {
    padding: 5px;
    display: inline-flex;
    align-items: baseline;
    margin: 0;
}

.logo > a {
    text-decoration: none;
    color: var(--clrs);
}

.logo > a > p > div.logo-p {
    color: var(--clro);
    padding: 3px;
    font-size: 40px;
}

.b3 {
    display: none;
}

.bb-slider::after {
    content: '';
    display: block;
    width: 0;
    height: 2px;
    background: var(--clro);
    transition: width 0.4s;
}

.bb-slider:hover::after {
    width: 100%;
    transition: width 0.4s;
}

.navlinks ul {
    display: flex;
    list-style: none;
}

.navlinks ul a li {
    padding: 10px;
    color: var(--clrs);
    font-weight: 800;
}

.navlinks ul a {
    text-decoration: none;
}

@media screen and (max-width: 480px) {
    .logo-box {
        margin-left: 0;
    }
    .navlinks ul {
        flex-wrap: wrap;
        justify-content: center;
    }
}