:root {
  --clrs: rgb(255, 255, 255);
  --clrl: rgba(255, 255, 255, 0.808);
  --clro: rgb(255, 153, 28);
  --clrol: rgba(255, 153, 28, 0.468);
  --bgs: #222;
  --bgl: rgb(33, 30, 29);
  --bga: rgba(63, 63, 63, 0.808);
}

/* hide scroll bar */
::-webkit-scrollbar {
  display: none;
}

body {
  margin: 0;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--bgs);
  color: var(--clrs);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.hlt {
  color: var(--clro);
}

.lt {
  color: var(--clrl);
}
